import * as BackboneHandler from './backbone-handlers';
import { zipTriggersWithPass } from './util';
import { ReduxTriggers, ReduxEnd$ } from './redux-triggers';

// * ------------------------------------------------ before mutation

// *  zipTriggersWithPass  map
